import {useState, useEffect} from "react";
import {Button, Grid, IconButton, TextField} from "@mui/material";
import DeleteIcon from '@mui/icons-material/Delete';

const NameIdForm = ({key,onChangeName, onChangeId, getId,getName ,deleteIcon, delAction}) => {
    const columnSize = deleteIcon ? 5 : 5;
    return (
        <Grid container spacing={2} py={1}>
            <Grid item xs={columnSize}>
                <TextField fullWidth id="outlined-basic-name" label="Name" variant="outlined" value={getName()} onChange={(e) => onChangeName(e)}/>
            </Grid>
            <Grid item xs={columnSize}>
                <TextField fullWidth id="outlined-basic-id" label="Id" variant="outlined" value={getId()} onChange={e => onChangeId(e)}/>

            </Grid>

             {deleteIcon && (
                <Grid item xs={12 - columnSize*2 } style={{textAlign: 'center',marginTop: "10px"}}>
                    <IconButton aria-label="delete" onClick={() => delAction(key)}>
                        <DeleteIcon/>
                    </IconButton>
                </Grid>
            )}


        </Grid>
    );
};

export default NameIdForm;