import React, { useState, useEffect } from "react";
import { DataGrid } from '@mui/x-data-grid';
import { Button, TextField, Container, Typography, Paper } from '@mui/material';

function JsonUtil() {
  const [jsonData, setJsonData] = useState('');
  const [fileName, setFileName] = useState('');
  const [rows, setRows] = useState([]);
  const [columns, setColumns] = useState([]);
  const [error, setError] = useState('');

  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.ctrlKey && event.key === 's') {
        event.preventDefault();
        handleConvert();
        setFileName("");
      }
    };
    window.addEventListener('keydown', handleKeyDown);
    return () => {
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [jsonData, fileName]);

  const handleJsonChange = (event) => {
    setJsonData(event.target.value);
    previewData(event.target.value);
  };

  const handleFileNameChange = (event) => {
    setFileName(event.target.value);
  };

  const previewData = (data) => {
    try {
      const parsedData = JSON.parse(data);
      const rowsData = parsedData['data'].map((video, index) => ({
        id: index,
        title: generateBeautifulFilename(video['title']),
        uuid: extractUuid(video['content'][0]['videoUrl'])[0]
      }));

      setRows(rowsData);
      setColumns([
        { field: 'id', headerName: 'ID', width: 70 },
        { field: 'title', headerName: 'Title', width: 200, editable: true },
        { field: 'uuid', headerName: 'UUID', width: 300, editable: true }
      ]);
      setError('');
    } catch (error) {
      console.error("Error:", error);
      setError("Invalid JSON data. Please enter valid JSON.");
      setRows([]);
      setColumns([]);
    }
  };

  const handleConvert = () => {
    if (!jsonData.trim() || !fileName.trim()) {
      alert("Please enter JSON data and filename.");
      return;
    }

    try {
      const data = JSON.parse(jsonData);
      const csvContent = [];

      for (const video of data['data']) {
        const beautifulFilename = generateBeautifulFilename(video['title']);
        const uuids = extractUuid(video['content'][0]['videoUrl']);
        const line = `${beautifulFilename},${uuids[0]}`;
        csvContent.push(line);
      }

      const csvString = csvContent.join("\n");
      const blob = new Blob([csvString], { type: "text/csv;charset=utf-8" });
      const link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = fileName + ".csv";
      link.click();
    } catch (error) {
      console.error("Error:", error);
      setError("Invalid JSON data. Please enter valid JSON.");
    }
  };

  const handleOpenPreview = () => {
    if (rows.length === 0) {
      alert("No data available for preview. Please enter valid JSON data.");
      return;
    }

    const queryParams = rows.map(row => `${encodeURIComponent(row.title)}=${encodeURIComponent(row.uuid)}`).join('&');
    const previewUrl = `http://localhost:3000?${queryParams}`;
    window.open(previewUrl, '_blank');
  };

  const handleProcessRowUpdate = (newRow) => {
    const updatedRows = rows.map(row => row.id === newRow.id ? newRow : row);
    setRows(updatedRows);
    return newRow;
  };

  function extractUuid(text) {
    const pattern = /[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}/g;
    const matches = text.match(pattern) || [];
    return matches;
  }

  function generateBeautifulFilename(filename) {
    const maxLength = 50;
    const words = filename.split(/[_\s]+/).map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase());
    let beautifulFilename = words.join('').replace(/[\/\\:*?"<>|]/g, '_');

    return beautifulFilename.length > maxLength ? beautifulFilename.slice(0, maxLength - 3) + '...' : beautifulFilename;
  }

  return (
    <Container>
      <Typography variant="h4" gutterBottom>JSON to CSV Converter</Typography>
      <TextField
        label="Enter JSON data"
        multiline
        rows={10}
        value={jsonData}
        onChange={handleJsonChange}
        fullWidth
        variant="outlined"
        margin="normal"
      />
      <TextField
        label="Enter filename (without extension)"
        value={fileName}
        onChange={handleFileNameChange}
        fullWidth
        variant="outlined"
        margin="normal"
      />
      <Button variant="contained" color="primary" onClick={handleConvert}>
        Convert to CSV
      </Button>
      <Button variant="contained" color="secondary" onClick={handleOpenPreview} style={{ marginLeft: '10px' }}>
        Open Preview
      </Button>
      {error && <Typography color="error">{error}</Typography>}
      <Paper style={{ height: 400, width: '100%', marginTop: 20 }}>
        <DataGrid
          rows={rows}
          columns={columns}
          processRowUpdate={handleProcessRowUpdate}
          disableSelectionOnClick
        />
      </Paper>
    </Container>
  );
}

export default JsonUtil;
