import logo from './logo.svg';
import './App.css';
import {
    Typography,
    Grid,
    TextField,
    Box,
    Button,
    Paper,
    LinearProgress,
    IconButton,
    ListItemButton
} from "@mui/material";
import Stack from '@mui/material/Stack';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import DownloadIcon from '@mui/icons-material/Download';
import AddIcon from '@mui/icons-material/Add';
import {useState, useEffect} from "react";
import NameIdForm from "./NameIdForm";
import SimpleSnackbar from "./SimpleSnackbar";

class DownloadVideoToServer {
    constructor(name, id, onStart, progressUpdate, onCompletion, timeout) {
        this.name = name;
        this.id = id;
        this.onStart = onStart;
        this.progressUpdate = progressUpdate;
        this.onCompletion = onCompletion;
        this.DL_URL = '/api/create_task';
        this.PRG_URL = "/api/progress/";
        this.GET_URL = '';
        this.progress = 0.0;
        this.completed = 0;
        this.task_id = '';
        this.timeout = timeout;
        this.retry = 0;
    }

    async startDownload() {
        const dl_headers = {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
            "Access-Control-Allow-Methods": "DELETE, POST, GET, OPTIONS",
            "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept Content-Type, Authorization, X-Requested-With"
        };

        const body = {
            'name': this.name,
            'id': this.id
        };

        try {
            const response = await fetch(this.DL_URL, {
                method: 'POST',
                headers: dl_headers,
                body: JSON.stringify(body),
            });

            const data = await response.json();
            const task_id = data.task_id;
            if (task_id) {
                this.onStart();
                this.task_id = task_id;
            }

            await this.trackProgress(this.task_id);

        } catch (error) {
            console.error('Error:', error);
            console.log('Retrying');
            if (this.retry < 3 && this.task_id !== '') {
                this.retry++;
                await this.startDownload();
            }
        }
    }

    async trackProgress(task_id) {
        const trackHeaders = {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
        };

        return new Promise((resolve, reject) => {
            const interval_id = setInterval(async () => {
                try {
                    const response = await fetch(`${this.PRG_URL}${task_id}`, {
                        method: 'GET',
                        headers: trackHeaders,
                    });

                    const data = await response.json();

                    if (data.status === "completed" || data.status === "failed") {
                        this.completed = 1;
                        this.onCompletion(data);
                        clearInterval(interval_id);
                        resolve(data);
                    }

                    this.progressUpdate(data);

                } catch (error) {
                    console.log(error);
                    clearInterval(interval_id);
                    reject(error);
                }
            }, this.timeout);
        });
    }
};

function App() {
    const [dl_state, setDl_state] = useState([{name: "", id: ""}]);
    const [downloaded, setDownloaded] = useState([]);
    const [task_id, setTask_id] = useState('');
    const [output, setOutput] = useState('');
    const [progress, setProgress] = useState(0);
    const [open, SetOpen] = useState(false);

    const startDownload = async () => {
        for (let index = 0; index < dl_state.length; index++) {
            const row = dl_state[index];
            const name = row.name;
            const id = row.id;

            const onStart = (task_id) => setTask_id(task_id);
            const trackProgress = (data) => {
                setOutput(data.progress.str);
                const n = dl_state.length;
                setProgress((data.progress.progress / n) + (100 / n * index));
            };
            const handleCompletion = (data) => {
                const url = `/api/${data.url}`;
                if (!downloaded.includes(url))
                    setDownloaded(prevDownloaded => [...prevDownloaded, url]);
            }

            const dl = new DownloadVideoToServer(name, id, onStart, trackProgress, handleCompletion, 500);
            await dl.startDownload(); // Wait for the current download to complete before starting the next one
        }
    };

    const addRow = () => {
        setDl_state([...dl_state, {name: "", id: ""}]);
    }

    const popRow = (index) => {
        const array = [...dl_state];
        array.splice(index, 1);
        setDl_state(array);
    }

    const setName = (index, name) => {
        const array = [...dl_state];
        array[index].name = name;
        setDl_state(array);
    }

    const setId = (index, id) => {
        const array = [...dl_state];
        array[index].id = id;
        setDl_state(array);
    }

    const fetchName = (index) => dl_state.length > index ? dl_state[index].name : "";
    const fetchId = (index) => dl_state.length > index ? dl_state[index].id : "";

    useEffect(() => {
        const urlParams = new URLSearchParams(window.location.search);
        try {
            const newEntries = [];
            urlParams.forEach((value, key) => {
                const [id, name] = [value, key];
                if (name && id) {
                    newEntries.push({name, id});
                }
            });

            // Filter out duplicates
            const uniqueEntries = newEntries.filter((entry, index, self) =>
                index === self.findIndex((e) => e.name === entry.name && e.id === entry.id)
            );

            setDl_state(uniqueEntries);
        } catch (err) {
            console.error('Error parsing URL parameters:', err);
        }
    }, []);

    return (
        <div>
            <SimpleSnackbar
                        open={open}
                        message="Copied to clipboard"
                        action={() => SetOpen(false)}
                        handleClose={() => SetOpen(false)}
                    />
            <Box sx={{flexGrow: 1}} p={4} id={"box"}>
                <Grid container spacing={2}>


                    {
                        dl_state.map((item, index) =>
                            <NameIdForm onChangeName={(e) => setName(index, e.target.value)}
                                        onChangeId={(e) => setId(index, e.target.value)}
                                        getName={() => fetchName(index)}
                                        getId={() => fetchId(index)}
                                        key={index}
                                        deleteIcon={index > 0}
                                        delAction={() => popRow(index)}/>
                        )
                    }

                    <Grid item xs={6} style={{textAlign: 'right', marginTop: '10px'}}>
                        <Button variant="contained" endIcon={<AddIcon/>} onClick={() => addRow()}>Add A Row </Button>
                    </Grid>
                    <Grid item xs={6} style={{textAlign: 'left', marginTop: '10px'}}>
                        <Button variant="contained" endIcon={<DownloadIcon/>}
                                onClick={() => startDownload()}>Download </Button>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper variant="outlined" sx={{p: 2, minHeight: 100}}>
                            <Typography variant="body1" component="div" style={{whiteSpace: 'pre-wrap'}}>
                                {output}
                            </Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <LinearProgress variant="determinate" value={progress}/>
                    </Grid>
                </Grid>
            </Box>

            <Stack spacing={2} margin={12}>
                {downloaded.length > 0 && (
                    <div>
                        <Button variant="contained" endIcon={<ContentCopyIcon/>} onClick={() => SetOpen(true)}>
                            Copy Links to Clipboard
                        </Button>
                        {downloaded.map((url, index) => {
                            if (!(index > dl_state.length - 1))
                                return (
                                    <ListItemButton key={index}>
                                        <Button variant="contained" endIcon={<DownloadIcon/>} href={url} download>
                                            Download {dl_state[index].name} {index + 1}
                                        </Button>
                                    </ListItemButton>
                                )
                        })}
                    </div>
                )}
            </Stack>
        </div>
    );
}

export default App;
